@import url(https://fonts.googleapis.com/css?family=Roboto);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --color-primary: #e5bd5b;
  --color-secondary: #f2f2f2;
  --color-complementary: #191919;
}

html {
  background: #191919;
  background: var(--color-complementary);
}

body {
  font-family: 'Roboto', sans-serif;
}

.App {
  text-align: center;
  background-color: #191919;
  background-color: var(--color-complementary);
  padding: 0 30px;
}

.App-logo {
  width: 100%;
  margin-top: 60px;
  margin-bottom: 45px;
}

.App-header {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  color: #f2f2f2;
  color: var(--color-secondary);
  max-width: 640px;
  margin-left: auto;
  margin-right: auto;
  position: relative;
}

.App-header .archive-link {
  position: absolute;
  top: 30px;
  right: 30px;
  bottom: auto;
  left: 50%;
  width: 100%;
  text-align: right;
  max-width: 520px;
  -webkit-transform: translate3d(-50%, 0, 0);
          transform: translate3d(-50%, 0, 0);
  color: #e5bd5b;
  color: var(--color-primary);
  text-decoration: underline;
}

.App-header .archive-link:hover,
.App-header .archive-link:focus,
.App-header .archive-link:active {
  color: #e5bd5b;
  color: var(--color-primary);
  text-decoration: none;
}

.App-header p {
  text-align: justify;
  width: 100%;
  max-width: 520px;
  margin-left: auto;
  margin-right: auto;
}

.App-header p em {
  color: #e5bd5b;
  color: var(--color-primary);
}

.App-header .subscription-form {
  width: 100%;
  max-width: 520px;
  margin: 0 auto 60px;
}

.App-header .subscription-form > div {
  display: flex;
  width: 100%;
  margin-top: 30px;
  margin-bottom: 60px;
  flex-direction: row;
  position: relative;
}

.App-header .subscription-form > div > div {
  position: absolute;
  top: auto;
  right: auto;
  bottom: 0;
  left: 0;
  -webkit-transform: translate3d(0, 60px, 0);
          transform: translate3d(0, 60px, 0);
  text-align: left;
}

.App-header .subscription-form button {
  background: #e5bd5b;
  background: var(--color-primary);
  color: #191919;
  color: var(--color-complementary);
  border: 0;
  border-radius: 0;
  padding: 0;
  height: 45px;
  line-height: 45px;
  width: 130px;
  font-size: 1em;
  font-weight: 600;
  margin: 0 0 0 15px;
  cursor: pointer;
  -webkit-transition: all .3s ease;
  transition: all .3s ease;
  outline: 0;
}

.App-header .subscription-form button:hover,
.App-header .subscription-form button:focus,
.App-header .subscription-form button:active {
  -webkit-transform: translate3d(0, -5px, 0);
          transform: translate3d(0, -5px, 0);
}

.App-header .subscription-form input[type="email"] {
  width: 100%;
  background: transparent;
  border: 0;
  border-radius: 0;
  border-bottom: 1px solid #e5bd5b;
  border-bottom: 1px solid var(--color-primary);
  color: #f2f2f2;
  color: var(--color-secondary);
  font-size: 1em;
  outline: 0;
}

.App-header .subscription-form input[type="email"]::-webkit-input-placeholder {
  font-size: 1em;
  color: #f2f2f2;
  color: var(--color-secondary);
  opacity: 1;
}

.App-header .subscription-form input[type="email"]::-moz-placeholder {
  font-size: 1em;
  color: #f2f2f2;
  color: var(--color-secondary);
  opacity: 1;
}

.App-header .subscription-form input[type="email"]:-ms-input-placeholder {
  font-size: 1em;
  color: #f2f2f2;
  color: var(--color-secondary);
  opacity: 1;
}

.App-header .subscription-form input[type="email"]::-ms-input-placeholder {
  font-size: 1em;
  color: #f2f2f2;
  color: var(--color-secondary);
  opacity: 1;
}

.App-header .subscription-form input[type="email"]::placeholder {
  font-size: 1em;
  color: #f2f2f2;
  color: var(--color-secondary);
  opacity: 1;
}

.App-link {
  color: #e5bd5b;
  color: var(--color-primary);
}

